#personal-details {
  #change-password {
    input {
      &:focus {
        background-color: blue;
        border-bottom: 0.1 em solid black;
        outline: none;
      }
    }
  }
  @media only screen and (max-width: 768px) {
    #change-password {
      text-align: center;
    }
  }
}
